.button {
  background-color: #408f95;
  color: #fff;
  font-family: "Gotu", sans-serif;
  font-size: 0.875rem;
  line-height: 1.5714;
  width: 12.625rem;
  height: 3.375rem;
  padding: 1rem 0;
  display: inline-block;
  transition: all 0.5s;
  border: 1px solid #408f95;
  overflow: visible;
  position: relative;
  z-index: 1;
  margin-bottom: 0.25rem;
}

.button::before {
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 4px;
  bottom: 4px;
  left: 4px;
  right: 4px;
  border: solid 1px #408f95; /* 外枠のスタイル */
  z-index: -1;
}

.button span {
  display: inline-block;
  content: "";
  width: 2.5rem;
  height: 0.375rem;
  border-bottom: 1px solid #fff;
  border-right: 1px solid #fff;
  transform: skew(50deg);
  margin-left: 0.375rem;
  position: relative;
  top: -0.25rem;
}

.button::after {
  background-color: #fff;
  position: absolute;
  top: 0;
  left: 0;
  content: "";
  width: 100%;
  height: 100%;
  transform: scale(0, 1);
  transform-origin: left top;
  transition: 0.2s cubic-bezier(0.45, 0, 0.55, 1);
  z-index: -1;
}

.button:hover {
  color: #408f95;
}

.button:hover::after {
  transform: scale(1, 1);
}

.button:hover span {
  border-bottom: 1px solid #408f95;
  border-right: 1px solid #408f95;
}

.button--white {
  background-color: #fff;
  border-color: #fff;
  color: #408f95;
}

.button--white::before {
  border-color: #fff;
}

.button--white span {
  border-color: #408f95;
}

.button--white:hover {
  color: #fff;
}

.button--white:hover::after {
  background-color: #408f95;
}

.button--white:hover span {
  border-bottom: 1px solid #fff;
  border-right: 1px solid #fff;
}

